.projects {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4rem;
    margin: 6rem auto;
}

.projects>h1 {
    font-size: 45px;
    font-weight: bold;
}

.projects>p {
    color: #b8b8b8;
    font-size: 18px;
    letter-spacing: .8px;
    line-height: 1.5em;
    max-width: 70%;
    text-align: center;    
}

